import inputData from '../../app/config/data.config.json';

function removeTint() {
    document.body.classList.remove('tint');
    document.body.classList.remove(window.abar.data.get('current_tint'));

    window.abar.activeFunctions.delete('addTint');
    window.abar.data.delete('current_tint');
}

function addTint({ elementID }) {
    const tintColour = inputData[elementID];

    if (window.abar.activeFunctions.has('addTint')) {
        window.abar.activeFunctions.get('addTint')();
    }

    document.body.classList.add('tint');
    document.body.classList.add(tintColour);
    window.abar.data.set('current_tint', tintColour);

    window.abar.activeFunctions.set('addTint', removeTint);
}

export {
    addTint,
    removeTint,
};



