// Rollbar
import Rollbar from 'rollbar';
import rollbarConfig from './app/config/rollbar.config.json';

// Accessabar
import Accessabar from './Accessabar/main';

const abRollbar = new Rollbar(rollbarConfig);

if (module.hot) {
    module.hot.accept();
}

// browser
window.Accessabar = Accessabar;

// commonjs
if (module.exports) {
    module.exports = Accessabar;
}

// es6 modules
export default Accessabar;



