function closeAccessabar() {
    window.abar.activeFunctions.forEach((func) => {
        func();
    });

    window.abar.activeFunctions.clear();
    window.abar.activeMenus = [];
    window.abar.events.flushEvents();
    window.abar.data.clear();

    document.body.removeChild(window.abar.parentElement);

    document.body.style.marginTop = '';
}

export {
    closeAccessabar,
};



