import {
    changeCSSAll,
    changeCSSBody,
} from './util';
import fontsConfig from '../../app/config/fonts.config.json';
import inputData from '../../app/config/data.config.json';

function fontIncrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');

    // For debug reasons
    if (!window.abar.data.has('original_font_sizes')) {
        const fontSizes = {
            elements: [],
            sizes: [],
        };

        for (const element of elements) {
            if (element.childElementCount <= 1) {
                fontSizes.elements.push(element);
                fontSizes.sizes.push(window.getComputedStyle(element, null).getPropertyValue('font-size'));
            }
        }

        window.abar.data.set('original_font_sizes', fontSizes);
    }

    for (const element of elements) {
        if (element.childElementCount <= 1) {
            $(element).css('font-size', '+=1px');
        }
    }
}

function fontDecrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');

    if (!window.abar.data.has('original_font_sizes')) {
        const fontSizes = {
            elements: [],
            sizes: [],
        };

        for (const element of elements) {
            if (element.childElementCount <= 1) {
                fontSizes.elements.push(element);
                fontSizes.sizes.push(window.getComputedStyle(element, null).getPropertyValue('font-size'));
            }
        }

        window.abar.data.set('original_font_sizes', fontSizes);
    }

    for (const element of elements) {
        if (element.childElementCount <= 1) {
            $(element).css('font-size', '-=1px');
        }
    }
}

function changeFont({ elementID }) {
    const currentFont = inputData[elementID];
    const fontList = fontsConfig;

    window.abar.data.set('current_font', currentFont);

    changeCSSAll('font-family', fontList[currentFont]);
}

function nextFont() {
    const fontList = fontsConfig;
    const keys = Object.keys(fontList);
    const currentIndex = keys.indexOf(window.abar.data.get('current_font'));
    let nextIndex = currentIndex + 1;

    if (nextIndex === keys.length) {
        nextIndex = 0;
    }

    changeCSSAll('font-family', fontList[keys[nextIndex]]);

    window.abar.data.set('current_font', keys[nextIndex]);
}

function prevFont() {
    const fontList = fontsConfig;
    const keys = Object.keys(fontList);
    const currentIndex = keys.indexOf(window.abar.data.get('current_font'));
    let nextIndex = currentIndex - 1;

    if (nextIndex === -1) {
        nextIndex = keys.length - 1;
    }

    changeCSSAll('font-family', fontList[nextIndex]);

    window.abar.data.set('current_font', keys[nextIndex]);
}

function changeFontColour({ elementID }) {
    const fontColour = inputData[elementID];

    changeCSSBody('color', fontColour);
    changeCSSAll('color', fontColour);
}

export {
    fontIncrease,
    fontDecrease,
    changeFont,
    nextFont,
    prevFont,
    changeFontColour,
};



