import inputData from '../../app/config/data.config.json';

function removeOverlay() {
    // Should overlay be set back to default size?
    const overlayTop = document.getElementById('ab_overlay_top');
    const overlayMiddle = document.getElementById('ab_overlay_middle');
    const overlayBottom = document.getElementById('ab_overlay_bottom');

    window.abar.events.removeDirectEvent('overlayMouseMove');
    window.abar.activeFunctions.delete('changeOverlay');

    document.body.removeChild(overlayTop);
    document.body.removeChild(overlayMiddle);
    document.body.removeChild(overlayBottom);
}

function changeOverlay({ event, elementID }) {
    const overlayOptions = inputData[elementID].split(',');
    const overlayRed = overlayOptions[0];
    const overlayGreen = overlayOptions[1];
    const overlayBlue = overlayOptions[2];
    const overlayAlpha = overlayOptions[3];
    const overlayMiddleHeight = window.abar.data.get('overlay_height');
    const mousePos = event.clientY;
    const overlayTop = document.createElement('div');
    const overlayMiddle = document.createElement('div');
    const overlayBottom = document.createElement('div');


    // TODO: magnifier checking
    if (window.abar.activeFunctions.has('changeOverlay')) {
        window.abar.activeFunctions.get('changeOverlay')();
    }

    if (window.abar.activeFunctions.has('enableRuler')) {
        window.abar.activeFunctions.get('enableRuler')();
    }

    if (window.abar.activeFunctions.has('magnify')) {
        window.abar.activeFunctions.get('magnify')();
    }

    overlayTop.setAttribute('style', `
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: ${mousePos - (overlayMiddleHeight / 2)}px;
        background-color: rgba(0, 0, 0, 0.9);
        z-index: 10000;
    `);

    overlayMiddle.setAttribute('style', `
        pointer-events: none;
        position: fixed;
        top: ${mousePos - (overlayMiddleHeight / 2)}px;
        left: 0;
        width: 100%;
        height: ${overlayMiddleHeight}px;
        background-color: rgba(${overlayRed}, ${overlayGreen}, ${overlayBlue}, ${overlayAlpha});
        z-index: 10000;
    `);

    overlayBottom.setAttribute('style', `
        position: fixed;
        top: ${mousePos + (overlayMiddleHeight / 2)}px;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.9);
        z-index: 10000;
    `);

    overlayTop.id = 'ab_overlay_top';
    overlayMiddle.id = 'ab_overlay_middle';
    overlayBottom.id = 'ab_overlay_bottom';

    document.body.appendChild(overlayTop);
    document.body.appendChild(overlayMiddle);
    document.body.appendChild(overlayBottom);

    window.abar.events.addDirectEvent('overlayMouseMove', 'mousemove', document);
    window.abar.activeFunctions.set('changeOverlay', removeOverlay);
}

function overlayIncrease({ event }) {
    let overlayMiddleHeight = window.abar.data.get('overlay_height');
    const mousePos = event.clientY;
    const overlayTop = document.getElementById('ab_overlay_top');
    const overlayMiddle = document.getElementById('ab_overlay_middle');
    const overlayBottom = document.getElementById('ab_overlay_bottom');

    overlayMiddleHeight += 10;
    window.abar.data.set('overlay_height', overlayMiddleHeight);

    overlayTop.style.height = `${mousePos - (overlayMiddleHeight / 2)}px`;
    overlayMiddle.style.height = `${overlayMiddleHeight}px`;
    overlayMiddle.style.top = `${mousePos - (overlayMiddleHeight / 2)}px`;
    overlayBottom.style.top = `${mousePos + (overlayMiddleHeight / 2)}px`;
}

function overlayDecrease({ event }) {
    let overlayMiddleHeight = window.abar.data.get('overlay_height');
    const mousePos = event.clientY;
    const overlayTop = document.getElementById('ab_overlay_top');
    const overlayMiddle = document.getElementById('ab_overlay_middle');
    const overlayBottom = document.getElementById('ab_overlay_bottom');

    if (overlayMiddleHeight > 20) {
        overlayMiddleHeight -= 10;
        window.abar.data.set('overlay_height', overlayMiddleHeight);

        overlayTop.style.height = `${mousePos - (overlayMiddleHeight / 2)}px`;
        overlayMiddle.style.height = `${overlayMiddleHeight}px`;
        overlayMiddle.style.top = `${mousePos - (overlayMiddleHeight / 2)}px`;
        overlayBottom.style.top = `${mousePos + (overlayMiddleHeight / 2)}px`;
    }
}

function overlayMouseMove(event) {
    const overlayTop = document.getElementById('ab_overlay_top');
    const overlayMiddle = document.getElementById('ab_overlay_middle');
    const overlayBottom = document.getElementById('ab_overlay_bottom');
    const overlayMiddleHeight = window.abar.data.get('overlay_height');
    const mousePos = event.clientY;

    if (mousePos - (overlayMiddleHeight / 2) >= 0) {
        overlayTop.style.height = `${mousePos - (overlayMiddleHeight / 2)}px`;
        overlayMiddle.style.top = `${mousePos - (overlayMiddleHeight / 2)}px`;
        overlayBottom.style.top = `${mousePos + (overlayMiddleHeight / 2)}px`;
    } else {
        overlayTop.style.height = 0;
        overlayMiddle.style.top = 0;
        overlayBottom.style.top = `${overlayMiddleHeight}px`;
    }
}

export {
    removeOverlay,
    changeOverlay,
    overlayIncrease,
    overlayDecrease,
    overlayMouseMove,
};



