function changeCSSAll(selector, value) {
    $('body *').not('#accessabar, #accessabar *, style, script').css(selector, value);
}

function changeCSSBody(selector, value) {
    $('body').not('#accessabar, #accessabar *, style, script').css(selector, value);
}

function computedStyle(el, styleProp) {
    let value;
    let sanitizedProp;
    const { defaultView } = el.ownerDocument;

    if (defaultView && defaultView.getComputedStyle) {
        sanitizedProp = styleProp.replace(/([A-Z])/g, '-$1').toLowerCase();
        return defaultView.getComputedStyle(el, null).getPropertyValue(sanitizedProp);
    } else if (el.currentStyle) {
        // sanitize property name to camelCase
        sanitizedProp = styleProp.replace(/-(\w)/g, (str, letter) => letter.toUpperCase());

        value = el.currentStyle[styleProp];
        // convert other units to pixels on IE
        if (/^\d+(em|pt|%|ex)?$/i.test(value)) {
            const oldLeft = el.style.left;
            const oldRsLeft = el.runtimeStyle.left;
            const element = el;

            element.runtimeStyle.left = element.currentStyle.left;
            element.style.left = value || 0;
            value = `${element.style.pixelLeft}px`;
            element.style.left = oldLeft;
            element.runtimeStyle.left = oldRsLeft;
        }
    }

    return value;
}

function getSelected() {
    const active = document.activeElement;

    switch (active.nodeName) {
    case 'INPUT':
    case 'TEXTAREA':
    case 'SELECT':
        return active;
    case 'DIV':
        if (active.contentEditable === true) {
            return active;
        }
        return false;
    default:
        return false;
    }
}

function capitalise(str) {
    return str.replace(/\S/, first => first.toUpperCase());
}

function linebreak(str) {
    return str.replace(/\n\n/g, '<p></p>').replace(/\n/g, '<br>');
}

function selectText(element) {
    let range;
    let selection;

    if (document.body.createTextRange) {
        range = document.body.createTextRange();
        range.moveToElementText(element);
        range.select();
    } else if (window.getSelection) {
        selection = window.getSelection();
        range = document.createRange();
        range.selectNodeContents(element);
        selection.removeAllRanges();
        selection.addRange(range);
    }
}

function ping() {
    console.log('pong');
}

export {
    changeCSSAll,
    changeCSSBody,
    computedStyle,
    getSelected,
    capitalise,
    linebreak,
    selectText,
    ping,
};



