function checkMenuClose(event) {
    const active = event.target || event.srcElement;
    let parent = active.parentElement;
    let insideMenu = false;

    // console.log(active);

    while (parent != null) {
        // console.log(parent);
        if (parent.classList) {
            if (parent.classList.contains('sub-menu')
                || parent.classList.contains('menu')
            ) {
                insideMenu = true;
                break;
            }
        }

        parent = parent.parentElement;
    }

    // console.log(insideMenu);

    if (!insideMenu) {
        /* eslint-disable */
        for (const menu of window.abar.activeMenus) {
            menu.style.visibility = 'hidden';
        }
        /* eslint-enable */
        window.abar.activeMenus = [];
    }
}

function keyboardMenuEnter(event) {
    const key = event.key || event.keyCode || event.which;
    const tabbedElement = document.activeElement;
    const ab = document.getElementById('accessabar');

    if (key === 'Enter' || key === 13) {
        if (ab.contains(tabbedElement)) {
            tabbedElement.click();
        }
    }
}

export {
    checkMenuClose,
    keyboardMenuEnter,
};




