import { computedStyle } from './util';

function removeMagnify() {
    const glass = document.getElementById('glassOfMagnifier');

    window.abar.events.removeDirectEvent('magnifyMouseMove');
    window.abar.events.removeDirectEvent('magnifyScroll');
    window.abar.events.removeDirectEvent('magnifyResize');
    window.abar.activeFunctions.delete('magnify');

    window.abar.data.delete('glass');
    window.abar.data.delete('zoomed_page');
    window.abar.data.delete('ab_zoomed');

    document.body.removeChild(glass);
}

function magnify(event) {
    const glass = document.createElement('div');
    const zoomedPage = document.createElement('div');
    const documentBg = computedStyle(document.body, 'background') ? computedStyle(document.body, 'background') : 'white';

    if (window.abar.activeFunctions.has('magnify')) {
        window.abar.activeFunctions.get('magnify')();
    }

    if (window.abar.activeFunctions.has('changeOverlay')) {
        window.abar.activeFunctions.get('changeOverlay')();
    }

    if (window.abar.activeFunctions.has('enableRuler')) {
        window.abar.activeFunctions.get('enableRuler')();
    }

    glass.id = 'glassOfMagnifier';
    glass.style.visibility = 'hidden';
    zoomedPage.id = 'zoomedPageOfMagnifier';
    zoomedPage.setAttribute('style', `
        background: ${documentBg}
        width: ${document.body.clientWidth}px;
        height: auto;
        visibility: hidden;
    `);
    zoomedPage.innerHTML = document.body.innerHTML;

    glass.appendChild(zoomedPage);
    document.body.appendChild(glass);

    const abInZoomed = zoomedPage.querySelector('#accessabar');
    const glassRect = glass.getBoundingClientRect();
    const spacer = document.createElement('div');

    spacer.style.height = document.body.style.marginTop;
    abInZoomed.insertAdjacentElement('afterend', spacer);

    glass.style.top = `${(event.clientY + window.scrollY) - (glassRect.height / 2)}px`;
    glass.style.left = `${event.clientX - (glassRect.width / 2)}px`;
    zoomedPage.style.top = `${((glassRect.height / 3) - (Math.abs(event.clientY) * 2) - (2 * window.scrollY)) + 50}px`; // TODO: split into variables
    zoomedPage.style.left = `${glassRect.height - (Math.abs(event.clientX) * 2) - 50}px`;
    abInZoomed.style.top = `${window.scrollY}px`;

    glass.style.visibility = 'visible';
    zoomedPage.style.visibility = 'visible';

    window.abar.data.set('glass', glass);
    window.abar.data.set('zoomed_page', zoomedPage);
    window.abar.data.set('ab_zoomed', abInZoomed);

    window.abar.events.addDirectEvent('magnifyMouseMove', 'mousemove', document);
    window.abar.events.addDirectEvent('magnifyScroll', 'scroll', document);
    window.abar.events.addDirectEvent('magnifyResize', 'resize', window);
    window.abar.activeFunctions.set('magnify', removeMagnify);
}

function toggleMagnify({ event }) {
    if (window.abar.activeFunctions.has('magnify')) {
        window.abar.activeFunctions.get('magnify')();
    } else {
        magnify(event);
    }
}

function magnifyMouseMove(event) {
    const glass = window.abar.data.get('glass');
    const zoomedPage = window.abar.data.get('zoomed_page');
    const glassRect = glass.getBoundingClientRect();

    glass.style.top = `${(event.clientY + window.scrollY) - (glassRect.height / 2)}px`;
    glass.style.left = `${event.clientX - (glassRect.width / 2)}px`;
    zoomedPage.style.top = `${((glassRect.height / 3) - (Math.abs(event.clientY) * 2) - (2 * window.scrollY)) + 50}px`; // TODO: split into variables
    zoomedPage.style.left = `${glassRect.height - (Math.abs(event.clientX) * 2) - 50}px`;
}

function magnifyScroll() {
    const abInZoomed = window.abar.data.get('ab_zoomed');

    abInZoomed.style.top = `${window.scrollY}px`;
}

function magnifyResize() {
    const zoomedPage = window.abar.data.get('zoomed_page');

    zoomedPage.style.width = `${document.body.clientWidth}px`;
}

export {
    magnify,
    toggleMagnify,
    removeMagnify,
    magnifyMouseMove,
    magnifyScroll,
    magnifyResize,
};



