function lineSpacingIncrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');

    for (const element of elements) {
        $(element).css('line-height', '+=10%');
    }
}

function lineSpacingDecrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');
    
    for (const element of elements) {
        $(element).css('line-height', '-=10%');
    }
}

function charSpacingIncrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');

    for (const element of elements) {
        $(element).css('letter-spacing', '+=0.2px');
    }
}

function charSpacingDecrease() {
    const elements = $('body *').not('#accessabar, #accessabar *, style, script');

    for (const element of elements) {
        $(element).css('letter-spacing', '-=0.2px');
    }
}

export {
    lineSpacingIncrease,
    lineSpacingDecrease,
    charSpacingIncrease,
    charSpacingDecrease,
};



