// AB Components
import * as Elements from './elements';
import {
    moveBody,
    updateDialects,
    populateVoiceList,
    resetSR,
    handleResult,
    toggleReadHighlighted,
} from './functions';
import initKeybinds from './keybinds';
import EventHandler from './event';
import languages from '../app/config/languages.config.json';

// HTML and stylesheets
import abHtml from '../app/pug/accessabar.pug';
import '../app/scss';

/* eslint-disable class-methods-use-this */

class Accessabar {
    constructor(button = undefined) {
        this.elementsInit = [];
        window.abar = {
            activeMenus: [],
            activeFunctions: new Map(),
            mainClass: new Proxy(this, {}),
            events: new EventHandler(),
            data: new Map(),
        };

        if (button) {
            switch (typeof button) {
            case 'string':
                this.buttonElement = document.getElementById(button);
                break;
            case 'object':
                if (button.nodeType === Node.ELEMENT_NODE) {
                    this.buttonElement = button;
                }
                break;
            default:
                throw Error('[Accessabar] Error: The button id or element given was not of a correct type');
            }

            if (!this.buttonElement) {
                throw Error('[Accessabar] Error: Cannot find element with the given id');
            }

            this.initStartButton();
        }
    }

    init() {
        if (document.getElementById('accessabar')) {
            return;
        }

        this.renderAccessabar();

        window.abar.parentElement = document.getElementById('accessabar');
        this.elementsSubmenu = window.abar.parentElement.getElementsByClassName('sub-menu');
        this.elementsButton = window.abar.parentElement.querySelectorAll('[ab-label]');

        const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);

        this.initMenus();
        this.initButtons();
        this.initGlobalEvents();
        this.initDefaultValues();
        this.initTTS();
        if (isChrome) {
            this.initSR();
        }
        initKeybinds();
        moveBody();

        console.log('[Accessabar] Successfully Loaded!');
    }

    // Alias for init
    open() {
        this.init();
    }

    initStartButton() {
        window.abar.events.addDirectEvent(() => this.init(), 'click', this.buttonElement);
    }

    initMenus() {
        for (let i = 0; i < this.elementsSubmenu.length; i++) {
            const type = this.findType(this.elementsSubmenu[i]);

            switch (type) {
            case 'dropdown':
                this.elementsInit.push(new Elements.Dropdown(this.elementsSubmenu[i].parentNode, this.elementsSubmenu[i]));
                break;
            case 'side-menu':
                this.elementsInit.push(new Elements.SideMenu(this.elementsSubmenu[i].parentNode, this.elementsSubmenu[i]));
                break;
            default:
                break;
            }
        }
    }

    initButtons() {
        this.elementsButton.forEach((element) => {
            this.elementsInit.push(new Elements.Button(element));
        });
    }

    initGlobalEvents() {
        window.abar.events.addDirectEvent('checkMenuClose', 'click', window);
        window.abar.events.addDirectEvent('checkMenuClose', 'touchstart', window);
        window.abar.events.addDirectEvent('keyboardMenuEnter', 'keypress', window);
        window.abar.events.addDirectEvent('moveBody', 'resize', window);
    }

    initDefaultValues() {
        window.abar.data.set('overlay_height', 80);
        window.abar.data.set('ruler_height', 80);
        window.abar.data.set('pitch', 1);
        window.abar.data.set('rate', 1);
        window.abar.data.set('volume', 1);
    }

    initTTS() {
        const ttsDrop = window.abar.parentElement.querySelector('#tts_drop');
        const checkbox = window.abar.parentElement.querySelector('#tts_readhl');
        const ttsPitch = window.abar.parentElement.querySelector('#tts_pitch');
        const ttsRate = window.abar.parentElement.querySelector('#tts_rate');
        const ttsVol = window.abar.parentElement.querySelector('#tts_vol');
        const ttsVoices = window.abar.parentElement.querySelector('#tts_voices');

        window.abar.data.set('tts_runtime', window.speechSynthesis);

        populateVoiceList();

        if (window.speechSynthesis.onvoiceschanged !== undefined) {
            window.speechSynthesis.onvoiceschanged = populateVoiceList;
        }

        window.abar.events.addDirectEvent(event => event.preventDefault(), 'mousedown', ttsDrop);
        window.abar.events.addDirectEvent(toggleReadHighlighted, 'change', checkbox);
        window.abar.events.delegateEvent('pitchChange', 'change', ttsPitch);
        window.abar.events.delegateEvent('rateChange', 'change', ttsRate);
        window.abar.events.delegateEvent('volumeChange', 'change', ttsVol);
        window.abar.events.delegateEvent('voiceChange', 'change', ttsVoices);
    }

    initSR() {
        const srLanguages = window.abar.parentElement.querySelector('#sr_languages');
        const srDialect = window.abar.parentElement.querySelector('#sr_dialect');
        const srSpeak = window.abar.parentElement.querySelector('#sr_speak');
        const Speech = window.SpeechRecognition || window.webkitSpeechRecognition;
        const srRuntime = new Speech();

        srRuntime.onresult = handleResult;
        srRuntime.continuous = true;
        // srRuntime.interim = true;
        srRuntime.interimResults = true;
        srRuntime.onend = resetSR;
        srRuntime.onerror = err => console.log(err);
        srRuntime.lang = 'en-US';

        window.abar.data.set('sr_runtime', srRuntime);

        languages.forEach((language, index) => {
            srLanguages.options[index] = new Option(language[0], index);
        });

        srLanguages.selectedIndex = 8;

        updateDialects();
        srDialect.selectedIndex = 6;

        resetSR();

        window.abar.events.addDirectEvent('updateDialects', 'change', srLanguages);
        window.abar.events.delegateEvent('toggleSR', 'click', srSpeak);
    }

    findType(el) {
        if (el.parentNode.classList.contains('dropdown')) {
            return 'dropdown';
        }

        if (el.parentNode.classList.contains('side-menu')) {
            return 'side-menu';
        }

        return null;
    }

    renderAccessabar() {
        const bodyChildren = document.body.childNodes;
        const abNav = document.createElement('nav');

        document.body.insertBefore(abNav, bodyChildren[0]);
        abNav.outerHTML = abHtml();
    }
}

export default Accessabar;



