import buttonsConfig from '../app/config/buttons.config.json';

class Button {
    constructor(element) {
        this.element = element;
        this.init();
    }

    init() {
        /* eslint-disable */
        for (const [elementId, eventsArray] of Object.entries(buttonsConfig)) {
            if (this.element.id == elementId) {
                for (const obj of eventsArray) {
                    // console.log(obj);
                    window.abar.events.delegateEvent(obj.function_id, obj.type, this.element);
                }
            }
        }
        /* eslint-enable */
    }
}

class Menu {
    constructor(parent, menu) {
        this.parent = parent;
        this.button = parent.firstChild;
        // console.log(this.button);
        this.element = menu;
        this.init();
    }
}

class Dropdown extends Menu {
    init() {
        this.initEvents();
        this.positionMenu();
    }

    initEvents() {
        window.abar.events.delegateEvent(() => this.handleDropdown(), 'click', this.button);
        window.abar.events.addDirectEvent(() => this.positionMenu(), 'resize', window);
    }

    handleDropdown() {
        if (window.abar.activeMenus.includes(this.element) // No IE support
            || window.abar.activeMenus.indexOf(this.element) !== -1) {
            return;
        }

        if (window.abar.activeMenus.length > 0) {
            // console.log(this.element);
            // console.log(window.abar.activeMenus[0]);
            if (!window.abar.activeMenus[0].contains(this.element)) {
                /* eslint-disable */
                for (const menu of window.abar.activeMenus) {
                    // console.log(menu);
                    menu.style.visibility = 'hidden';
                }
                /* eslint-enable */

                window.abar.activeMenus = [];
            }
        }

        this.element.style.visibility = 'visible';
        window.abar.activeMenus.push(this.element);
    }

    positionMenu() {
        const menuRect = this.element.getBoundingClientRect();
        const parentRect = this.parent.getBoundingClientRect();


        if (parentRect.left + menuRect.width > window.innerWidth) {
            const offset = (parentRect.left + menuRect.width) - window.innerWidth;

            this.element.style.left = `${-offset}px`;
        } else if (this.element.getAttribute('style') ? this.element.getAttribute('style').includes('left') : false) {
            this.element.style.removeProperty('left');
        }
    }
}

class SideMenu extends Menu {
    init() {
        this.initEvents();
        this.positionMenu();
    }

    initEvents() {
        window.abar.events.delegateEvent(() => this.handleSideMenu(), 'click', this.button);
        window.abar.events.addDirectEvent(() => this.positionMenu(), 'resize', window);
    }

    handleSideMenu() {
        if (window.abar.activeMenus.includes(this.element) // No IE support
            || window.abar.activeMenus.indexOf(this.element) !== -1
        ) {
            return;
        }

        if (window.abar.activeMenus.length > 0) {
            // console.log(this.element);
            // console.log(window.abar.activeMenus[0]);
            if (!window.abar.activeMenus[0].contains(this.element)) {
                /* eslint-disable */
                for (const menu of window.abar.activeMenus) {
                    menu.style.visibility = 'hidden';
                }
                /* eslint-enable */

                window.abar.activeMenus = [];
            }

            if (window.abar.activeMenus.length >= 2) {
                window.abar.activeMenus[window.abar.activeMenus.length - 1].style.visibility = 'hidden';
                // delete window.abar.activeMenus[window.abar.activeMenus.length - 1]; // doesn't reorder array
                window.abar.activeMenus.splice(window.abar.activeMenus.length - 1, 1);
                // console.log(window.abar.activeMenus);
            }
        }

        this.element.style.visibility = 'visible';
        window.abar.activeMenus.push(this.element);
    }

    positionMenu() {
        const buttonRect = this.button.getBoundingClientRect();
        const menuRect = this.element.getBoundingClientRect();

        if ((window.innerWidth - (buttonRect.left + buttonRect.width)) < menuRect.width) {
            this.element.style.right = '100%';
            if (this.element.getAttribute('style').includes('left')) {
                this.element.style.removeProperty('left');
            }
        } else {
            this.element.style.left = '100%';
            if (this.element.getAttribute('style').includes('right')) {
                this.element.style.removeProperty('right');
            }
        }
    }
}

export {
    Button,
    SideMenu,
    Dropdown,
};



