import languages from '../../app/config/languages.config.json';
import {
    getSelected,
    capitalise,
    linebreak,
} from './util';

function updateDialects() {
    const srLanguages = window.abar.parentElement.querySelector('#sr_languages');
    const srDialect = window.abar.parentElement.querySelector('#sr_dialect');
    const list = languages[srLanguages.selectedIndex];

    // console.log(list);

    for (let i = srDialect.options.length - 1; i >= 0; i--) {
        srDialect.remove(i);
    }

    if (list.length > 1) {
        for (let i = 1; i < list.length; i++) {
            srDialect.options.add(new Option(list[i][1], list[i][0]));
        }

        // srDialect.style.visibility = 'visible';
    }
}

function resetSR() {
    const srSpeak = document.getElementById('sr_speak');

    window.abar.data.set('recognizing', false);
    srSpeak.innerHTML = 'Click to Speak';
}

function toggleSR() {
    const recognizing = window.abar.data.get('recognizing');
    const srRuntime = window.abar.data.get('sr_runtime');
    const srDialect = window.abar.parentElement.querySelector('#sr_dialect');
    const srSpeak = window.abar.parentElement.querySelector('#sr_speak');

    if (recognizing) {
        srRuntime.stop();
        resetSR();
    } else {
        srRuntime.lang = srDialect.value;
        srRuntime.start();
        window.abar.data.set('recognizing', true);
        srSpeak.innerHTML = 'Click to Stop';
    }
}

function handleResult({ event }) {
    const selectedInput = getSelected();
    const finalSpan = window.abar.parentElement.querySelector('#final_span');
    const interimSpan = window.abar.parentElement.querySelector('#interim_span');

    if (!selectedInput) {
        let final = finalSpan.innerHTML;
        let interim = '';

        for (let i = event.resultIndex; i < event.results.length; ++i) {
            if (event.results[i].isFinal) {
                final += event.results[i][0].transcript;
            } else {
                interim += event.results[i][0].transcript;
            }
        }

        final = capitalise(final);
        finalSpan.innerHTML = linebreak(final);
        interimSpan.innerHTML = linebreak(interim);
    } else {
        let final = selectedInput.value;

        for (let i = event.resultIndex; i < event.results.length; ++i) {
            if (event.results[i].isFinal) {
                final += event.results[i][0].transcript;
            }
        }

        final = capitalise(final);
        selectedInput.value = final;
    }
}

export {
    updateDialects,
    resetSR,
    toggleSR,
    handleResult,
};



