import Mousetrap from 'mousetrap';
import * as Functions from './functions';
import keybinds from '../app/config/keybinds.config.json';

/* eslint-disable no-restricted-syntax */

function initKeybinds() {
    for (const [key, obj] of Object.entries(keybinds)) {
        Mousetrap.bind(key, (event) => {
            let element;
            let elementID;

            if (obj.id) {
                element = document.getElementById(obj.id);
                elementID = element.id;
            }

            Functions[obj.function]({
                event,
                element,
                elementID,
            });
        });
    }
}

export default initKeybinds;



