function removeRuler() {
    // Should ruler be set back to default size?
    const ruler = document.getElementById('ab_ruler');

    window.abar.events.removeDirectEvent('rulerMouseMove');
    window.abar.activeFunctions.delete('enableRuler');

    document.body.removeChild(ruler);
}

function enableRuler(event) {
    // TODO: magnifier checking
    const ruler = document.createElement('div');
    const mousePos = event.clientY;
    const rulerHeight = window.abar.data.get('ruler_height');

    if (window.abar.activeFunctions.has('enableRuler')) {
        window.abar.activeFunctions.get('enableRuler')();
    }

    if (window.abar.activeFunctions.has('changeOverlay')) {
        window.abar.activeFunctions.get('changeOverlay')();
    }

    if (window.abar.activeFunctions.has('magnify')) {
        window.abar.activeFunctions.get('magnify')();
    }

    ruler.setAttribute('style', `
        position: fixed;
        top: ${mousePos + 15}px;
        left: 0;
        width: 100%;
        height: ${rulerHeight}px;
        background-color: rgba(0, 0, 0, 0.95);
        z-index: 10000;
    `);

    ruler.id = 'ab_ruler';

    document.body.appendChild(ruler);

    window.abar.events.addDirectEvent('rulerMouseMove', 'mousemove', document);
    window.abar.activeFunctions.set('enableRuler', removeRuler);
}

function toggleRuler({ event }) {
    if (window.abar.activeFunctions.has('enableRuler')) {
        window.abar.activeFunctions.get('enableRuler')();
    } else {
        enableRuler(event);
    }
}

function rulerIncrease() {
    const ruler = document.getElementById('ab_ruler');
    let rulerHeight = window.abar.data.get('ruler_height');

    rulerHeight += 10;
    window.abar.data.set('ruler_height', rulerHeight);

    ruler.style.height = `${rulerHeight}px`;
}

function rulerDecrease() {
    const ruler = document.getElementById('ab_ruler');
    let rulerHeight = window.abar.data.get('ruler_height');

    if (rulerHeight > 20) {
        rulerHeight -= 10;
        window.abar.data.set('ruler_height', rulerHeight);

        ruler.style.height = `${rulerHeight}px`;
    }
}

function rulerMouseMove(event) {
    const ruler = document.getElementById('ab_ruler');
    const mousePos = event.clientY;

    ruler.style.top = `${mousePos + 15}px`;
}

export {
    removeRuler,
    enableRuler,
    toggleRuler,
    rulerIncrease,
    rulerDecrease,
    rulerMouseMove,
};



