import { selectText } from './util';

function ttsHover(target) {
    if (target !== document.body && target.tagName !== 'HTML') {
        const ttsRuntime = window.abar.data.get('tts_runtime');
        const text = target.innerText;
        const utterThis = new window.SpeechSynthesisUtterance(text);

        if (ttsRuntime.speaking || ttsRuntime.pending) {
            ttsRuntime.cancel();
        }

        selectText(target);

        if (window.abar.data.has('tts_voice')) {
            utterThis.voice = window.abar.data.get('tts_voice');
        }

        utterThis.pitch = window.abar.data.get('pitch');
        utterThis.volume = window.abar.data.get('volume');
        utterThis.rate = window.abar.data.get('rate');

        ttsRuntime.speak(utterThis);
    }
}

function mouseoutFunc() {
    const ttsRuntime = window.abar.data.get('tts_runtime');

    if (ttsRuntime.speaking || ttsRuntime.pending) {
        ttsRuntime.cancel();
    }

    clearTimeout(window.abar.data.get('hover_timeout'));
    window.abar.events.removeDirectEvent('mouseoutFunc', 'mouseout');
}

function ttsHoverHandle(event) {
    const ttsRuntime = window.abar.data.get('tts_runtime');

    if (ttsRuntime.speaking || ttsRuntime.pending) {
        ttsRuntime.cancel();
    }

    window.abar.data.set('hover_timeout', setTimeout(ttsHover(event.target), 500));

    window.abar.events.addDirectEvent('mouseoutFunc', 'mouseout', event.target);
}

function ttsRemoveHover() {
    clearTimeout(window.abar.data.get('hover_timeout'));

    window.abar.events.removeDirectEvent('ttsHoverHandle', 'mouseover');
    window.abar.data.delete('hover_timeout');
    window.abar.activeFunctions.delete('ttsHoverStart');
}

function ttsHoverStart() {
    window.abar.events.addDirectEvent('ttsHoverHandle', 'mouseover', document);

    window.abar.activeFunctions.set('ttsHoverStart', ttsRemoveHover);
}

function ttsRead() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    const text = window.getSelection().toString();
    const utterThis = new window.SpeechSynthesisUtterance(text);

    if (ttsRuntime.speaking || ttsRuntime.pending) {
        ttsRuntime.cancel();
    }

    if (window.abar.data.has('tts_voice')) {
        utterThis.voice = window.abar.data.get('tts_voice');
    }

    utterThis.pitch = window.abar.data.get('pitch');
    utterThis.volume = window.abar.data.get('volume');
    utterThis.rate = window.abar.data.get('rate');

    ttsRuntime.speak(utterThis);
}

function ttsStop() {
    const ttsRuntime = window.abar.data.get('tts_runtime');

    if (window.abar.activeFunctions.has('ttsHoverStart')) {
        window.abar.activeFunctions.get('ttsHoverStart')();
    }

    ttsRuntime.cancel();
}

function ttsPause() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    ttsRuntime.pause();
}

function ttsResume() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    ttsRuntime.resume();
}

function ttsStart() {
    const { checked } = window.abar.parentElement.querySelector('#tts_readhl');

    if (checked) {
        ttsRead();
    } else {
        ttsHoverStart();
    }
}

function ttsToggle() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    const { paused } = ttsRuntime;

    if (paused) {
        ttsResume();
    } else {
        ttsStart();
    }
}

function readHighlightedDisable() {
    const readHL = window.abar.parentElement.querySelector('#tts_readhl');

    ttsStop();
    readHL.checked = false;
}

function readHighlightedEnable() {
    const readHL = window.abar.parentElement.querySelector('#tts_readhl');

    ttsStop();
    readHL.checked = true;
}

function toggleReadHighlighted() {
    ttsStop();
}

function populateVoiceList() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    const vList = document.getElementById('tts_voices');
    const voices = ttsRuntime.getVoices();
    const selectedIndex = vList.selectedIndex < 0 ? 0 : vList.selectedIndex;

    vList.innerHTML = '';

    voices.forEach((voice) => {
        const option = document.createElement('option');

        option.textContent = `${voice.name} (${voice.lang})`;

        if (voice.default) {
            option.textContent += ' -- DEFAULT';
        }

        option.setAttribute('data-lang', voice.lang);
        option.setAttribute('data-name', voice.name);
        vList.appendChild(option);
    });

    vList.selectedIndex = selectedIndex;
}

function pitchChange() {
    const pitchElement = document.getElementById('tts_pitch');

    window.abar.data.set('pitch', pitchElement.value);
}

function rateChange() {
    const rateElement = document.getElementById('tts_rate');

    window.abar.data.set('rate', rateElement.value);
}

function volumeChange() {
    const volElement = document.getElementById('tts_vol');

    window.abar.data.set('volume', volElement.value);
}

function voiceChange() {
    const ttsRuntime = window.abar.data.get('tts_runtime');
    const vList = document.getElementById('tts_voices');
    const voices = ttsRuntime.getVoices();

    window.abar.data.set('tts_voice', voices[vList.selectedIndex]);
}

function ttsCheckboxToggle() {
    const checkbox = window.abar.parentElement.querySelector('#tts_readhl');

    if (checkbox.checked) {
        checkbox.checked = false;
    } else {
        checkbox.checked = true;
    }
}

export {
    toggleReadHighlighted,
    readHighlightedEnable,
    readHighlightedDisable,
    populateVoiceList,
    pitchChange,
    rateChange,
    volumeChange,
    voiceChange,
    ttsToggle,
    ttsPause,
    ttsStop,
    mouseoutFunc,
    ttsHoverHandle,
    ttsCheckboxToggle,
};



