import {
    changeCSSAll,
    changeCSSBody,
} from './util';
import inputData from '../../app/config/data.config.json';

function changeTheme({ elementID }) {
    const theme = inputData[elementID].split(',');

    changeCSSBody('background-color', theme[0]);
    changeCSSAll('background-color', theme[0]);
    changeCSSBody('color', theme[1]);
    changeCSSAll('color', theme[1]);
}

export {
    changeTheme,
};



