import * as Functions from './functions';

class EventHandler {
    elementsBound = [];
    eventDetails = [];
    typesBound = [];
    directEventHandles = new Map();

    // TODO: add support for multiple events on the same element
    delegateEvent(callback, type, element) {
        switch (typeof callback) {
        case 'function':
        case 'string':
            if (!this.elementsBound.includes(element) || this.elementsBound.indexOf(element) === -1) {
                const arr = [
                    {
                        type,
                        callback,
                    },
                ];

                this.elementsBound.push(element);
                this.eventDetails.push(arr);
            }

            if (!this.typesBound.includes(type) || this.typesBound.indexOf(type) === -1) {
                this.typesBound.push(type);
                window.abar.parentElement.addEventListener(type, this);
            }

            break;
        default:
            throw Error('[Accessabar] Error (EventHandler.delegateEvent): callback must be a function or string');
        }
    }

    addDirectEvent(callback, type, element) {
        switch (typeof callback) {
        case 'function':
            this.directEventHandles.set(callback, {
                element,
                type,
            });
            element.addEventListener(type, callback);
            break;
        case 'string':
            this.directEventHandles.set(callback, {
                element,
                type,
            });
            element.addEventListener(type, Functions[callback]);
            break;
        default:
            throw Error('[Accessabar] Error (EventHandle.addDirectEvent): callback must be a function or string');
        }
    }

    // add support for types
    removeDirectEvent(callback, type = null) {
        const obj = this.directEventHandles.get(callback);

        if (obj.element !== undefined) {
            obj.element.removeEventListener(obj.type, Functions[callback]);
        }

        this.directEventHandles.delete(callback);
    }

    flushEvents() {
        this.typesBound.forEach((type) => {
            window.abar.parentElement.removeEventListener(type, this);
        });

        /* eslint-disable */
        for (const [functionName, obj] of this.directEventHandles) {
            if (obj.element !== undefined) {
                obj.element.removeEventListener(obj.type, Functions[functionName]);
            }
        }
        /* eslint-enable */

        this.elementsBound = [];
        this.eventDetails = [];
        this.typesBound = [];
        this.directEventHandles.clear();
    }

    handleEvent(event) {
        // console.log('[Accessabar] (EventHandler.handleEvent)');
        let element = event.target;
        let eventArray;

        // console.log(element);

        if (this.elementsBound.includes(element) || this.elementsBound.indexOf(element) !== -1) {
            eventArray = this.eventDetails[this.elementsBound.indexOf(element)];
        } else if (this.elementsBound.includes(element.parentNode) || this.elementsBound.indexOf(element.parentNode) !== -1) {
            const parent = element.parentNode;
            eventArray = this.eventDetails[this.elementsBound.indexOf(parent)];
            element = parent;

            // console.log(parent);
        } else {
            return;
        }

        console.log(eventArray);

        eventArray.forEach((obj) => {
            if (obj.type === event.type) {
                const elementID = element.id;

                switch (typeof obj.callback) {
                case 'string':
                    Functions[obj.callback]({
                        event,
                        element,
                        elementID,
                    });
                    break;
                case 'function':
                    obj.callback();
                    break;
                default:
                    throw Error(`[Accessabar] Error: Event callback is of type ${typeof obj.callback} when it should be a string or function`);
                }
            }
        });
    }
}

export default EventHandler;



